import React, { Component } from "react";
import { Divider, Form, Input } from "antd";
import DynamicFields from "./DynamicFields";
import "../StepThree.css";

class Beneficiaire extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleChange = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.stepThreeCallBack(values.beneficiaire);
      e.preventDefault();
    });
  };

  render() {
    const { form } = this.props;
    return (
      <div className="container">
        <Form onChange={this.handleChange}>
          <Divider orientation="left" className="first">
            Ajouter Beneficiaire
          </Divider>
          <DynamicFields
            {...form}
            name="beneficiaire"
            fields={[
              {
                name: "cin",
                field: () => (
                  <Input style={{ width: "30%" }} placeholder={"cin"} />
                ),
              },
              {
                name: "nom",
                field: () => (
                  <Input style={{ width: "30%" }} placeholder={"nom"} />
                ),
              },
              {
                name: "prenom",
                field: () => (
                  <Input style={{ width: "30%" }} placeholder={"prenom"} />
                ),
              },
              {
                name: "rib",
                field: () => (
                  <Input style={{ width: "30%" }} placeholder={"rib"} />
                ),
              },
            ]}
          />
        </Form>
      </div>
    );
  }
}

export default Form.create({
  name: "beneficiaire",
})(Beneficiaire);
