import "antd/dist/antd.css";
import { Form, Tabs } from "antd";
import React, { Component } from "react";
import Comptabilite from "./Comptabilite";
import GestionImpayes from "./GestionImpayes";
import ComptabiliteRetraite from "./ComptabiliteRetraite";

const { TabPane } = Tabs;
class Global extends Component {
  render() {
    return (
      <div>
        <Tabs type="card">
          <TabPane tab="Gestion des impays" key="1">
            <GestionImpayes />
          </TabPane>
          <TabPane tab="Comptabilit dcs " key="2">
            <Comptabilite />
          </TabPane>
          <TabPane tab="Comptabilit pargne retraite" key="3">
            <ComptabiliteRetraite />
          </TabPane>
        </Tabs>
      </div>
    );
  }
}

export default Form.create()(Global);
