import "antd/dist/antd.css";
import {
  Button,
  Col,
  Divider,
  Form,
  Icon,
  Input,
  message,
  Row,
  Select,
} from "antd";
import React, { Component } from "react";
let id = 0;
const { Option } = Select;
let current = 3;
class StepFive extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tarrifications: [],
      count: 0,
      current: 3,
      typeTar: "",
    };
  }

  remove = (k) => {
    const { form } = this.props;
    // can use data-binding to get
    const keys = form.getFieldValue("keys");
    // We need at least one passenger
    if (keys.length === 1) {
      return;
    }
    // can use data-binding to set
    form.setFieldsValue({
      keys: keys.filter((key) => key !== k),
    });
  };

  add = () => {
    if (this.state.typeTar !== "") {
      const { form } = this.props;
      // can use data-binding to get
      const keys = form.getFieldValue("keys");
      const nextKeys = keys.concat(id++);
      // can use data-binding to set
      // important! notify form to detect changes
      form.setFieldsValue({
        keys: nextKeys,
      });
    } else {
      message.warning("Choississez le type de tarrification");
    }
  };

  handleSubmited = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      current = this.state.current + 1;
      let data2 = [];
      for (var index = 0; index < values.keys.length; index++) {
        this.state.typeTar === "Taux"
          ? (data2 = {
              capitalMax: values.capitalMax[index],
              capitalMin: values.capitalMin[index],
              ageMax: values.ageMax[index],
              ageMin: values.ageMin[index],
              dureeMax: values.dureeMax[index],
              dureeMin: values.dureeMin[index],
              differeMax: values.differeMax[index],
              differeMin: values.differeMin[index],
              taux: values.tauxFor[index],
            })
          : (data2 = {
              capitalMax: values.capitalMax[index],
              capitalMin: values.capitalMin[index],
              ageMax: values.ageMax[index],
              ageMin: values.ageMin[index],
              dureeMax: values.dureeMax[index],
              dureeMin: values.dureeMin[index],
              differeMax: values.differeMax[index],
              differeMin: values.differeMin[index],
              forfait: values.tauxFor[index],
            });
        this.state.tarrifications.push(data2);
      }
      this.props.check(this.state.tarrifications, this.state.typeTar, current);
    });
  };

  prev() {
    const currentPrev = this.state.current - 1;
    this.setState({ currentPrev });
    this.props.check(
      this.state.tarrifications,
      this.state.typeTar,
      currentPrev
    );
  }

  handleChangeTarrification = (value) => {
    this.setState({
      typeTar: value,
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayoutWithOutLabel = {
      wrapperCol: {
        xs: { span: 24, offset: 0 },
        sm: { span: 20, offset: 4 },
      },
    };
    getFieldDecorator("keys", { initialValue: [] });
    const keys = getFieldValue("keys");
    const formItems = keys.map((k, index) => (
      <div>
        <Row span={24}>
          <Col span={4}>
            <Form.Item label="Dure" required={false}>
              {getFieldDecorator(`dureeMin[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Mois" placeholder="Min" />)}
              {getFieldDecorator(`dureeMax[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Mois" placeholder="Max" />)}
            </Form.Item>
          </Col>
          <Col span={4} offset={1}>
            <Form.Item label="Age" required={false}>
              {getFieldDecorator(`ageMin[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Ans" placeholder="Min" />)}
              {getFieldDecorator(`ageMax[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Ans" placeholder="Max" />)}
            </Form.Item>
          </Col>
          <Col span={4} offset={1}>
            <Form.Item label="Capital" required={false}>
              {getFieldDecorator(`capitalMin[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Dhs" placeholder="Min" />)}
              {getFieldDecorator(`capitalMax[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Dhs" placeholder="Max" />)}
            </Form.Item>
          </Col>
          <Col span={4} offset={1}>
            <Form.Item label="Diffr" required={false}>
              {getFieldDecorator(`differeMin[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Mois" placeholder="Min" />)}
              {getFieldDecorator(`differeMax[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs obligatoire.",
                  },
                ],
              })(<Input addonAfter="Mois" placeholder="Max" />)}
            </Form.Item>
          </Col>
          <Col span={4} offset={1}>
            <Form.Item label={this.state.typeTar} required={false} key={k}>
              {getFieldDecorator(`tauxFor[${k}]`, {
                validateTrigger: ["onChange", "onBlur"],
                rules: [
                  {
                    required: true,
                    message: "Champs Obligatoire.",
                  },
                ],
              })(
                this.state.typeTar === "Taux" ? (
                  <Input addonAfter="%" placeholder="Taux" />
                ) : (
                  <Input addonAfter="%" placeholder="Forfait" />
                )
              )}
              {keys.length > 1 ? (
                <Icon
                  className="dynamic-delete-button"
                  type="minus-circle-o"
                  onClick={() => this.remove(k)}
                />
              ) : null}
            </Form.Item>
          </Col>
        </Row>
      </div>
    ));

    return (
      <Row>
        <Col span={24}>
          <Form id="laststep" onSubmit={this.handleSubmited}>
            <Divider orientation="left">Tarification</Divider>
            <Row gutter={[16, 8]}>
              <Col span={4} offset={1}>
                Type de tarification:{" "}
              </Col>
              <Col flex="auto" span={12}>
                <Select
                  onChange={this.handleChangeTarrification}
                  placeholder="- Veuillez slectionner -"
                  style={{ width: "16em" }}
                >
                  <Option value="Taux">Taux</Option>
                  <Option value="Forfait">Forfait</Option>
                </Select>
              </Col>
              <Col span={24}>{formItems}</Col>
            </Row>
            <Form.Item {...formItemLayoutWithOutLabel}>
              <Button type="dashed" onClick={this.add} style={{ width: "60%" }}>
                <Icon type="plus" /> Ajouter
              </Button>
            </Form.Item>
            <Col span={24} offset={1}>
              <Form.Item>
                {this.state.current === 3 && (
                  <Button type="primary" htmlType="submit">
                    Suivant <Icon type="arrow-right" />
                  </Button>
                )}
                {this.state.current > 0 && (
                  <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                    <Icon type="arrow-left" />
                    Prcdent
                  </Button>
                )}
              </Form.Item>
            </Col>
          </Form>
        </Col>
      </Row>
    );
  }
}
export default Form.create({ name: "global_state" })(StepFive);
