/* eslint-disable react/prop-types */
import "./AppHeader.css";

import { Badge, Card, Dropdown, Icon, Input, Layout, Menu } from "antd";
import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import {
  getUserNotifications,
  updateNotification
} from "../menutakaful/Administration/AdministartionAPI";

const Header = Layout.Header;
const { SubMenu } = Menu;

class AppHeader extends Component {
  state = {
    notification: [],
    count: 0
  };
  constructor(props) {
    super(props);
    this.handleMenuClick = this.handleMenuClick.bind(this);
  }

  handleMenuClick({ key }) {
    if (key === "logout") {
      this.props.onLogout();
    }
  }

  componentDidMount() {
    this.loadNotification();
  }

  async loadNotification() {
    let responce = await getUserNotifications();
    let count = 0;
    responce.data.content.forEach(element => {
      if (element.read === false) count++;
    });

    this.setState({ count, notification: responce.data.content });
  }

  async readNotification(id, notification) {
    if (notification.read === false) {
      notification.read = true;
      delete notification.id;
      await updateNotification(id, notification);
      this.setState({ count: this.state.count - 1 });
    }
  }
  render() {
    let menuItems;
    if (this.props.currentUser) {
      menuItems = (
        <Menu
          mode="horizontal"
          theme="light"
          selectedkeys={[this.props.location.pathname]}
          onClick={this.handleMenuClick}
          style={{ backgroundColor: "#fafafa" }}
        >
          <Menu.Item
            key="logout"
            style={{ float: "right", height: "64px", color: "grey" }}
          >
            Bonjour, {this.props.currentUser.firstName.toUpperCase()}{" "}
            {this.props.currentUser.lastName.toUpperCase()}
            <Icon type="logout" style={{ marginLeft: "10px" }} />
          </Menu.Item>

          <Menu.Divider
            type="vertical"
            style={{ float: "right", height: "64px" }}
          />

          <SubMenu
            style={{ float: "right", height: "64px" }}
            title={
              <span className="submenu-title-wrapper">
                <Icon
                  type="user"
                  style={{ marginLeft: "10px", color: "rgba(0, 0, 0, 0.65)" }}
                />
              </span>
            }
          >
            <Menu.ItemGroup title={this.props.currentUser.email}>
              <Menu.Divider />
              <Menu.Item key="profile" style={{ color: "rgba(0, 0, 0, 0.65)" }}>
                Profile
              </Menu.Item>
              <Menu.Item key="logout" style={{ color: "rgba(0, 0, 0, 0.65)" }}>
                Se dconnecter
              </Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>

          <Menu.Divider
            type="vertical"
            style={{ float: "right", height: "64px" }}
          />

          <Menu.Item style={{ float: "right", height: "64px" }}>
            <Dropdown
              style={{ float: "right", height: "64px" }}
              trigger={["click"]}
              overlay={
                <Menu>
                  {" "}
                  {this.state.notification.map(element => (
                    <Menu.Item key={element.id} style={{ padding: "0px  5px" }}>
                      {element.read === false ? (
                        <Card
                          onClick={() =>
                            this.readNotification(element.id, element)
                          }
                          style={{ backgroundColor: "#F0F8FF" }}
                        >
                          <p>{element.message}</p>
                        </Card>
                      ) : (
                        <Card
                          onClick={() =>
                            this.readNotification(element.id, element)
                          }
                          style={{ backgroundColor: "#FFFFFF" }}
                        >
                          <p>{element.message}</p>
                        </Card>
                      )}
                    </Menu.Item>
                  ))}
                </Menu>
              }
            >
              <Badge count={this.state.count}>
                {" "}
                <Icon type="notification" />
              </Badge>
            </Dropdown>
          </Menu.Item>

          <Menu.Divider
            type="vertical"
            style={{ float: "right", height: "64px" }}
          />

          <Menu.Item style={{ float: "left", height: "64px" }}>
            <Input
              placeholder="   Recherche sur Atlas Takaful Systme "
              className="search-header"
              prefix={
                <Icon type="search" style={{ color: "rgba(0,0,0,.25)" }} />
              }
              style={{ color: "rgba(0,0,0,.25)" }}
            />
          </Menu.Item>
        </Menu>
      );
    } else {
      menuItems = (
        <Menu
          style={{ height: "64px" }}
          mode="horizontal"
          selectedkeys={[this.props.location.pathname]}
        >
          <Menu.Item key="/login" style={{ float: "right", height: "64px" }}>
            <Link to="/login">Login</Link>
          </Menu.Item>
          <Menu.Item key="/signup" style={{ float: "right", height: "64px" }}>
            <Link to="/signup">Signup</Link>
          </Menu.Item>
        </Menu>
      );
    }

    return (
      <Header style={{ padding: "0px", height: "auto" }}>{menuItems}</Header>
    );
  }
}

export default withRouter(AppHeader);
