import "antd/dist/antd.css";
import { Button, Card, Drawer } from "antd";
import React, { Component } from "react";
import { MyContext } from "./ConsultDecesContrat";
import Avenants from "./DetailsTabs/Avenants";
import Contrats from "./DetailsTabs/Contrats";
import General from "./DetailsTabs/General";
import Produits from "./DetailsTabs/Produits";
import Sinistre from "./DetailsTabs/Sinistre";

const tabListNoTitle = [
  {
    key: "general",
    tab: "Gnral"
  },
  {
    key: "contrat",
    tab: "Contrat"
  },
  {
    key: "produit",
    tab: "Produit"
  },
  {
    key: "avenant",
    tab: "Avenant(s)"
  },
  {
    key: "sinistre",
    tab: "Sinistre"
  }
];

class DetailsContrat extends Component {
  state = {
    key: "general",
    noTitleKey: "general",
    visible: false
  };
  showDrawer = () => {
    this.setState({
      visible: true
    });
  };
  onClose = () => {
    this.setState({
      visible: false
    });
  };
  onTabChange = (key, type) => {
    this.setState({ [type]: key });
  };
  contentListNoTitle = {
    general: <General />,
    contrat: <Contrats />,
    produit: <Produits />,
    avenant: <Avenants />,
    sinistre: <Sinistre />
  };

  render() {
    return (
      <div>
        <MyContext.Consumer>
          {context => (
            <React.Fragment>
              <a
                href="#top"
                type="primary"
                shape="circle"
                icon="edit"
                size="small"
                onClick={this.showDrawer}
              >
                {context.state.numeroContrat}
              </a>
              <Drawer
                title={
                  <div style={{ fontSize: "18px" }}>
                    Dtails du Contrat : {context.state.numeroContrat}
                  </div>
                }
                width={1000}
                onClose={this.onClose}
                visible={this.state.visible}
                bodyStyle={{ paddingBottom: 80 }}
              >
                <Card
                  tabList={tabListNoTitle}
                  activeTabKey={this.state.noTitleKey}
                  onTabChange={key => {
                    this.onTabChange(key, "noTitleKey");
                  }}
                >
                  {this.contentListNoTitle[this.state.noTitleKey]}
                </Card>
                <div className="submit-cancel">
                  <Button
                    type="primary"
                    className="not-rounded"
                    onClick={this.onClose}
                  >
                    Fermer
                  </Button>
                </div>
              </Drawer>
            </React.Fragment>
          )}
        </MyContext.Consumer>
      </div>
    );
  }
}
export default DetailsContrat;
