/* eslint-disable react/prop-types */
import React, { Component } from "react";
import "antd/dist/antd.css";
import {
  notification,
  Button,
  Row,
  Col,
  Card,
  Form,
  Input,
  Select,
  Icon,
  Modal,
  Popconfirm,
  Checkbox,
  Table
} from "antd";
import ComponentTitle from "../../../util/Title/ComponentTitle";
import { getAllPartenaire } from "../../Parametrage/partenaire/PartenaireAPI";
import { addProduitMrb } from "./ProduitMRBAPI";
import { getExclusion } from "../../EchangeFichiersInformatiques/EchangeFileAPI";
import { getPeriodicite } from "../../GestionContrats/ContratsAPI";
const { Option } = Select;
let key = 0;
let exclusFamille = "MRB";
class AjoutProduit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0,
      periodicite: [],
      exclusions: [],
      tarifications: [],
      data: [],
      visible: false,
      partenaire: []
    };
    this.columns = [
      {
        title: "Nature du bien",
        dataIndex: "natureBien",
        key: "natureBien"
      },
      {
        title: "Age min",
        dataIndex: "ageMin",
        key: "ageMin"
      },
      {
        title: "Age max",
        dataIndex: "ageMax",
        key: "ageMax"
      },
      {
        title: "Valeur min",
        dataIndex: "valeurMin",
        key: "valeurMin"
      },
      {
        title: "Valeur max",
        dataIndex: "valeurMax",
        key: "valeurMax"
      },
      {
        title: "Montant de Contribution",
        dataIndex: "tauxContribution",
        key: "tauxContribution"
      },
      {
        title: "Opration",
        dataIndex: "operation",
        render: (text, record) =>
          this.state.data.length >= 1 ? (
            <Popconfirm
              title="etes-vous sur?"
              onConfirm={() => this.handleDelete(record.key)}
            >
              <a href="#top">
                <Icon type="delete" theme="twoTone" />
              </a>
            </Popconfirm>
          ) : null
      }
    ];
  }

  handleDelete = keyMrb => {
    const data = [...this.state.data];
    this.setState({ data: data.filter(item => item.keyMrb !== keyMrb) });
  };

  async getAllPeriodicite() {
    let response = await getPeriodicite();
    this.setState({
      periodicite: response.data.content
    });
  }

  componentDidMount() {
    this.getAllPeriodicite();
    this.getAllExclusion(exclusFamille);
    this.getAllPartenaires();
  }

  async ajouterProduitMrb(values) {
    try {
      let response = await addProduitMrb(values);

      if (response.status === 200) {
        notification.success({
          message: "l'ajout du produit est bien fait"
        });
      }
      this.props.record.history.push("/ProduitMrb");
    } catch (error) {
      notification.error({
        message: "Takaful",
        description:
          error.message ||
          "Dsol! Quelque chose s'est mal pass Veuillez ressayer!"
      });
    }
  }

  async getAllPartenaires() {
    let response = await getAllPartenaire();
    this.setState({
      partenaire: response.data.content
    });
  }

  async getAllExclusion(famille) {
    let response = await getExclusion(famille);
    this.setState({
      exclusions: response.data.content
    });
  }

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      let request = {
        key: key,
        natureBien: values.natureBien,
        ageMax: values.ageMax,
        ageMin: values.ageMin,
        valeurMax: values.valeurMax,
        valeurMin: values.valeurMin,
        tauxContribution: values.tauxContribution
      };
      this.state.data.push(request);
      key = key + 1;
    });

    this.setState({ visible: false });
  };

  handleCancel = () => {
    this.setState({ visible: false });
  };

  handleClose = () => {
    this.props.form.setFieldsValue({
      natureBien: "",
      ageMax: "",
      ageMin: "",
      valeurMax: "",
      valeurMin: "",
      tauxContribution: ""
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      let exclus = [];
      values.exclusion.forEach(element => {
        exclus.push({ id: element });
      });
      values.exclusion = [...exclus];
      let periodicite = [];
      values.periodicites.forEach(element => {
        periodicite.push({ id: element });
      });
      values.periodicites = [...periodicite];

      let data2 = [];

      for (let index = 0; index < this.state.data.length; index++) {
        let data = {
          ageMax: this.state.data[index].ageMax,
          ageMin: this.state.data[index].ageMin,
          valeurMax: this.state.data[index].valeurMax,
          valeurMin: this.state.data[index].valeurMin,
          natureBienAssure: this.state.data[index].natureBien,
          tauxContribution: this.state.data[index].tauxContribution
        };
        data2.push(data);
      }

      let requestModel = {
        code: values.code,
        libelle: values.libelle,
        natureParticipant: values.natureSouscripteur,
        natureAssure: values.natureAssure,
        assureDiffParticipant: values.assureDiffParticipant,
        natureBienAssure: values.natureBien,
        franchiseIncendie: values.franchiseIncendie,
        franchiseBrisGlace: values.franchiseBrisGlace,
        franchiseDegatEaux: values.franchiseDegatEaux,
        franchiseCatastropheNaturelles: values.franchiseCatastrophesNaturelles,
        tauxTaxe: values.tauxTaxe,
        montantMaximumGarantie: values.montantMaximum,
        fraisGestion: values.fraisGestion,
        tvaFraisGestion: values.tvaFraisGestion,
        delaiPrescription: values.delaiEnAttente,
        delaiPrescriptionSansSouscription: values.delaiSansSouscription,
        exclusionsProduit: values.exclusion,
        periodicitesMrb: values.periodicites,
        partenaireId: values.intermediaire,
        tarificationsMrb: data2
      };

      this.ajouterProduitMrb(requestModel);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { visible } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 10 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 }
      }
    };
    return (
      <div>
        <ComponentTitle title="Nouveau produit MRB" />
        <Modal
          visible={visible}
          title="Ajout de tarification"
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          afterClose={this.handleClose}
          footer={[
            <Button
              key="back"
              onClick={this.handleCancel}
              className="not-rounded"
            >
              Annuler
            </Button>,
            <Button
              key="submit"
              type="primary"
              onClick={this.handleOk}
              className="not-rounded"
            >
              Ajouter
            </Button>
          ]}
        >
          <Form {...formItemLayout} hideRequiredMark>
            <Col>
              <Form.Item label="Nature du bien ">
                {getFieldDecorator("natureBien")(
                  <Select placeholder="Slctionnez...">
                    <Option value="Appartement">Appartement</Option>
                    <Option value="Maison">Maison</Option>
                    <Option value="Villa">Villa</Option>
                    <Option value="Btiment">Btiment</Option>
                  </Select>
                )}
              </Form.Item>
              <Form.Item label="Age du bien" required={false}>
                {getFieldDecorator("ageMin", {
                  validateTrigger: ["onChange", "onBlur"],
                  rules: [
                    {
                      required: true,
                      message: "Champs obligatoire."
                    }
                  ]
                })(
                  <Input
                    addonAfter="Ans"
                    className="not-rounded"
                    placeholder="Min"
                  />
                )}
                {getFieldDecorator("ageMax", {
                  validateTrigger: ["onChange", "onBlur"],
                  rules: [
                    {
                      required: true,
                      message: "Champs obligatoire."
                    }
                  ]
                })(
                  <Input
                    addonAfter="Ans"
                    className="not-rounded"
                    placeholder="Max"
                  />
                )}
              </Form.Item>
              <Form.Item label="Valeur du bien" required={false}>
                {getFieldDecorator("valeurMin", {
                  validateTrigger: ["onChange", "onBlur"],
                  rules: [
                    {
                      required: true,
                      message: "Champs obligatoire."
                    }
                  ]
                })(
                  <Input
                    addonAfter="Dhs"
                    className="not-rounded"
                    placeholder="Min"
                  />
                )}
                {getFieldDecorator("valeurMax", {
                  rules: [
                    {
                      required: true,
                      message: "Champs obligatoire."
                    }
                  ]
                })(
                  <Input
                    addonAfter="Dhs"
                    className="not-rounded"
                    placeholder="Max"
                  />
                )}
              </Form.Item>
              <Form.Item label="Montant de la contribution" required={false}>
                {getFieldDecorator("tauxContribution", {
                  rules: [
                    {
                      required: true,
                      message: "Champs obligatoire."
                    }
                  ]
                })(
                  <Input
                    className="not-rounded"
                    addonAfter="Dhs"
                    placeholder="Montant de la contribution"
                  />
                )}
              </Form.Item>
            </Col>
          </Form>
        </Modal>
        <div style={{ background: "#ECECEC", padding: "20px" }}>
          <Form id="formadd" {...formItemLayout}>
            <Row gutter={12}>
              <Col span={12}>
                <Card
                  title="Informations Gnrales"
                  bordered={false}
                  style={{ marginTop: 16 }}
                >
                  <Form.Item label="Libelle">
                    {getFieldDecorator("libelle", {
                      rules: [
                        {
                          required: true,
                          message: "entrez la libelle"
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <Form.Item label="Abrviation">
                    {getFieldDecorator("code", {
                      rules: [
                        {
                          required: true,
                          message: "entrez le code "
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <Form.Item label="Intermdiaire">
                    {getFieldDecorator("intermediaire", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(
                      <Select
                        placeholder="Selectionnez ..."
                        onChange={this.handleChange}
                      >
                        {this.state.partenaire.map(element => {
                          return (
                            <Option
                              key={element.id}
                              value={element.id}
                              label={element.raisonSocial}
                            >
                              {element.raisonSocial}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </Form.Item>
                  <Form.Item label="Periodicit">
                    {getFieldDecorator("periodicites", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(
                      <Select
                        mode="multiple"
                        placeholder="Selectionnez ..."
                        onChange={this.handleChange}
                      >
                        {this.state.periodicite.map(element => {
                          return (
                            <Option
                              key={element.id}
                              value={element.id}
                              label={element.abb}
                            >
                              {element.libelle}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </Form.Item>
                  <Form.Item label="Exclusion">
                    {getFieldDecorator("exclusion", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(
                      <Select
                        mode="multiple"
                        placeholder="Selectionnez ..."
                        onChange={this.handleChange}
                      >
                        {this.state.exclusions.map(element => {
                          return (
                            <Option
                              key={element.id}
                              value={element.id}
                              label={element.exclusionNom}
                            >
                              {element.exclusionNom}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </Form.Item>
                </Card>
                <Card
                  title="Franchise"
                  bordered={false}
                  style={{ marginTop: 16 }}
                >
                  <Form.Item>
                    {getFieldDecorator("franchiseIncendie", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Checkbox>Franchise incendie</Checkbox>)}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator("franchiseBrisGlace", {
                      rules: [
                        {
                          required: true,
                          message: "entrez le code "
                        }
                      ]
                    })(<Checkbox>Franchise bris de glace</Checkbox>)}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator("franchiseDegatEaux", {
                      rules: [
                        {
                          required: true,
                          message: "entrez le code "
                        }
                      ]
                    })(<Checkbox>Franchise dgats des eaux</Checkbox>)}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator("franchiseCatastrophesNaturelles", {
                      rules: [
                        {
                          required: true,
                          message: "entrez le code "
                        }
                      ]
                    })(<Checkbox>Franchise catastrophes naturelles</Checkbox>)}
                  </Form.Item>
                </Card>
              </Col>
              <Col span={12}>
                <Card
                  title="Paramtres globaux"
                  bordered={false}
                  style={{ marginTop: 16 }}
                >
                  <Form.Item label="Frais de gestion Wakala">
                    {getFieldDecorator("fraisGestion", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <Form.Item label="TVA frais de gestion">
                    {getFieldDecorator("tvaFraisGestion", {
                      rules: [
                        {
                          required: true,
                          message: "entrez le code "
                        }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <Form.Item label="Montant max de garantie">
                    {getFieldDecorator("montantMaximum", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Input addonAfter="DH" />)}
                  </Form.Item>
                  <Form.Item label="Taux taxe">
                    {getFieldDecorator("tauxTaxe", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Input addonAfter="%" />)}
                  </Form.Item>
                  <label>
                    <Form.Item label=" Dlai de prescription de loffre EART" />
                  </label>
                  <Form.Item label="En lattente du retour client">
                    {getFieldDecorator("delaiEnAttente", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Input addonAfter="Jours" />)}
                  </Form.Item>
                  <Form.Item label="Client sans souscription">
                    {getFieldDecorator("delaiSansSouscription", {
                      rules: [
                        {
                          required: true
                        }
                      ]
                    })(<Input addonAfter="Jours" />)}
                  </Form.Item>
                </Card>
                <Card
                  title="Souscripteur/ Assur"
                  bordered={false}
                  style={{ marginTop: 16 }}
                >
                  <Form.Item>
                    {" "}
                    {getFieldDecorator("assureDiffParticipant", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(
                      <Checkbox defaultChecked={false}>
                        Assur diffrent du participant
                      </Checkbox>
                    )}
                  </Form.Item>
                  <Form.Item label="Nature du participant/souscripteur">
                    {getFieldDecorator("natureSouscripteur", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(<Input />)}
                  </Form.Item>
                  <Form.Item label="Nature de l'assur">
                    {getFieldDecorator("natureAssure", {
                      rules: [
                        { required: true, message: "Champs Obligatoire !" }
                      ]
                    })(<Input />)}
                  </Form.Item>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Card
                  title="Tarification"
                  bordered={false}
                  style={{ marginTop: 16 }}
                >
                  <Table
                    rowClassName="editable-row"
                    columns={this.columns}
                    dataSource={this.state.data}
                  />
                  <Button
                    type="primary"
                    className="not-rounded"
                    onClick={this.showModal}
                    style={{ marginTop: 8 }}
                  >
                    <Icon type="plus" />
                    Ajouter
                  </Button>
                </Card>
              </Col>
            </Row>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                className="not-rounded"
                style={{ width: "25%", marginTop: 40 }}
                onClick={this.handleSubmit}
              >
                Enregistrer
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
}

export default Form.create()(AjoutProduit);
