/* eslint-disable react/prop-types */
import "antd/dist/antd.css";

import {
  Button,
  Col,
  DatePicker,
  Divider,
  Drawer,
  Form,
  Icon,
  Input,
  InputNumber,
  notification,
  Radio,
  Row,
  Select,
  AutoComplete
} from "antd";
import React, { Component } from "react";
import { createPersonnePhysique } from "../../../../../Participants/ParticipantAPI";
import { getAllVille } from "../../../../../../util/VillesAPI";

const { Option } = Select;

class AddSouscripteur extends Component {
  constructor(props) {
    super(props);
    this.state = { visible: false, villes: [] };
  }
  async getAllVilles() {
    let responseVille = await getAllVille();
    let helpArray = [];
    responseVille.data.forEach(element => {
      const object = element.name;
      helpArray.push(object);
    });
    this.setState({ villes: helpArray });
  }

  componentDidMount() {
    this.getAllVilles();
  }
  showDrawer = () => {
    this.setState({
      visible: true
    });
  };

  onClose = () => {
    this.setState({
      visible: false
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.dateNaissance = values.dateNaissance.format("DD-MM-YYYY");
        values.adressVille = values.adressVille.label;
        this.createPersonnePhysique(values);
      }
    });
  };
  onChangeVille = value => {
    console.log(value);
    this.setState({
      ville: value.label,
      adresscomplet:
        this.state.vois +
        " " +
        this.state.complement +
        " " +
        this.state.numero +
        " " +
        this.state.ville +
        " " +
        this.state.pays
    });
  };

  async createPersonnePhysique(model) {
    try {
      let response = await createPersonnePhysique(model);

      if (response.status === 200) {
        this.onClose();
        notification.success({
          message: "personne bien ajoute"
        });
        this.props.personCreation(response.data);
      }
    } catch (error) {
      this.onClose();

      if (error.response.data.message === "souscripteur already exists") {
        notification.error({
          message: "une personne et deja inscrit avec le meme cin ."
        });
      } else {
        notification.error({
          message: "Takaful",
          description:
            error.message || "Sorry! Something went wrong. Please try again!"
        });
      }
    }
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div>
        {this.props.titre === "Cration d'un nouveau Participant" ? (
          <Button
            type="primary"
            onClick={this.showDrawer}
            style={{ borderRadius: "0px", marginBottom: "10px" }}
            className="nouveau-btn"
          >
            <Icon type="user-add" /> Nouveau participant
          </Button>
        ) : (
          <Button
            type="primary"
            style={{ backgroundColor: "#77cc6d", borderColor: "#77cc6d" }}
            shape="circle"
            icon="plus"
            onClick={this.showDrawer}
          />
        )}

        <Drawer
          title={this.props.titre}
          width={720}
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
        >
          <Form layout="vertical" hideRequiredMark onSubmit={this.handleSubmit}>
            <Row>
              <Col span={12}>
                <Form.Item label="Prospect">
                  {getFieldDecorator("isProspect", {
                    valuePropName: "checked",
                    initialValue: true
                  })(
                    <Radio.Group buttonStyle="solid" defaultValue={true}>
                      <Radio.Button value={true}>Oui</Radio.Button>
                      <Radio.Button value={false}>Non</Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Prnom">
                  {getFieldDecorator("prenom", {
                    rules: [
                      {
                        required: true,
                        message: "Tapez le prnom de souscripteur"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      placeholder="Tapez le prnom de souscripteur"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Nom">
                  {getFieldDecorator("nom", {
                    rules: [
                      {
                        required: true,
                        message: "Tapez le prnom de souscripteur"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="Tapez le nom de souscripteur"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="CIN">
                  {getFieldDecorator("cin", {
                    rules: [
                      {
                        required: true,
                        message: "Svp entr votre CIN!"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      placeholder="Tapez le CIN de souscripteur"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Date de naissance" hasFeedback>
                  {getFieldDecorator("dateNaissance", {
                    rules: [
                      {
                        required: true,
                        message: "entre la date de naissance"
                      }
                    ]
                  })(<DatePicker></DatePicker>)}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Matricule">
                  {getFieldDecorator("matricule", {
                    rules: [{ required: false }]
                  })(
                    <Input
                      className="not-rounded"
                      placeholder="Tapez le matricule de souscripteur"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Profession">
                  {getFieldDecorator("profession", {
                    rules: [
                      {
                        required: true,
                        message: "Slectionnez une profession"
                      }
                    ]
                  })(
                    <Select placeholder="Profession">
                      {this.props.professions.map(element => {
                        return (
                          <Option key={element.id} value={element.libelle}>
                            {element.libelle}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Situation familiale ">
                  {getFieldDecorator("situationFamiliale", {
                    rules: [
                      {
                        required: true,
                        message: "Slectionnez une Situation familiale"
                      }
                    ]
                  })(
                    <Select placeholder="Situation familiale">
                      {this.props.setuation.map(element => {
                        return (
                          <Option key={element.id} value={element.value}>
                            {element.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Sexe">
                  {getFieldDecorator("sexe", {
                    rules: [{ required: true, message: "Slectionnez le sexe" }]
                  })(
                    <Select placeholder="Slectionnez le sexe">
                      {this.props.sexe.map(element => {
                        return (
                          <Option key={element.id} value={element.value}>
                            {element.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Nationalit">
                  {getFieldDecorator("nationalite", {
                    rules: [
                      { required: true, message: "Entrez une Nationalit" }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="Entrez une Nationalit"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Nombre d'enfants">
                  {getFieldDecorator("nombreEnfants", {
                    rules: [
                      {
                        required: true,
                        pattern: new RegExp(/^[0-9\b]+$/),
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <InputNumber
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="entrez le nombre d'enfants"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="RIB">
                  {getFieldDecorator("rib", {
                    rules: [
                      {
                        required: true,
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="RIB"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Tlphone">
                  {getFieldDecorator("numeroDeTelephone", {
                    rules: [
                      {
                        required: true,
                        pattern: new RegExp("^[0][5-7]{1}[0-9]{8}$"),
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="Numero de telephone"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="N Tiers">
                  {getFieldDecorator("numTiers", {
                    rules: [
                      {
                        required: true,
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="N Tiers"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={24}>
                <h4>Adress :</h4>
                <Divider></Divider>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Pays">
                  {getFieldDecorator("adressPays", {
                    rules: [{ required: true, message: "selecte un pays" }]
                  })(<Input />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Ville">
                  {getFieldDecorator("adressVille", {
                    rules: [{ required: true, message: "selecte une ville" }]
                  })(
                    <AutoComplete
                      size="default"
                      dataSource={this.state.villes}
                      onSelect={this.onChangeVille}
                      allowClear={false}
                      labelInValue
                      placeholder="ajouter une ville"
                      filterOption={(inputValue, option) =>
                        option.props.children
                          .toUpperCase()
                          .indexOf(inputValue.toUpperCase()) !== -1
                      }
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Vois">
                  {getFieldDecorator("adressVois", {
                    rules: [{ required: true, message: "selecte une vois" }]
                  })(
                    <Select placeholder="Vois">
                      {this.props.vois.map(element => {
                        return (
                          <Option key={element.id} value={element.value}>
                            {element.name}
                          </Option>
                        );
                      })}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Code Postal">
                  {getFieldDecorator("adressCodePostal", {
                    rules: [
                      {
                        required: true,
                        pattern: new RegExp(/^[0-9\b]+$/),
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <InputNumber
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="entrez le Code Postal"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="Numero">
                  {getFieldDecorator("adressNumero", {
                    rules: [
                      {
                        required: true,
                        pattern: new RegExp(/^[0-9\b]+$/),
                        message: "Format invalide !"
                      }
                    ]
                  })(
                    <InputNumber
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="entrez le Numero"
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Complement">
                  {getFieldDecorator("adressComplement")(
                    <Input
                      className="not-rounded"
                      style={{ width: "100%" }}
                      placeholder="entrez le un complement"
                    />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={24}>
                <Form.Item label="Adress Complete">
                  {getFieldDecorator("adressComplete")(
                    <Input className="not-rounded" style={{ width: "100%" }} />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <div
              style={{
                position: "absolute",
                right: 0,
                bottom: 0,
                width: "100%",
                borderTop: "1px solid #e9e9e9",
                padding: "10px 16px",
                background: "#fff",
                textAlign: "right"
              }}
            >
              <Button
                className="not-rounded"
                onClick={this.onClose}
                style={{ marginRight: 8 }}
              >
                Annuler
              </Button>
              <Button className="not-rounded" type="primary" htmlType="submit">
                Enregistrer
              </Button>
            </div>
          </Form>
        </Drawer>
      </div>
    );
  }
}

export default Form.create()(AddSouscripteur);
