export function getPrestationSearch(
  auxiliaire,
  status,
  produit,
  typeAuxiliaire,
  nomParticipant,
  contrat
) {
  if (!localStorage.getItem(ACCESS_TOKEN)) {
    return Promise.reject("No access token set.");
  }
  return request({
    url:
      API_BASE_URL +
      "/private/prestation-honoraire?auxiliaire=" +
      auxiliaire +
      "&status=" +
      status +
      "&produit=" +
      produit +
      "&type-auxiliaire=" +
      typeAuxiliaire +
      "&participant=" +
      nomParticipant +
      "&contrat=" +
      contrat,
    method: "GET"
  });
}
export function setPrestationStatut(id, statut) {
  if (!localStorage.getItem(ACCESS_TOKEN)) {
    return Promise.reject("No access token set.");
  }
  return request({
    url: API_BASE_URL + "/private/reglement/" + id + "/" + statut,
    method: "GET"
  });
}

export function addPrestationHonoraire(id, statut, model) {
  if (!localStorage.getItem(ACCESS_TOKEN)) {
    return Promise.reject("No access token set.");
  }
  return request({
    url: API_BASE_URL + "/private/reglementHonoraire/" + id + "/" + statut,
    data: model,
    method: "POST"
  });
}
