import "antd/dist/antd.css";
import { Button, Card, Drawer } from "antd";
import React, { Component } from "react";
import { MyContext } from "./Comptabilite.js";
import Cotisation from "./Cotisation";

const tabListNoTitle = [
  {
    key: "cotisation",
    tab: "Cotisation"
  }
];

class DetailsCotisation extends Component {
  state = {
    key: "cotisation",
    noTitleKey: "cotisation",
    visible: false
  };

  showDrawer = () => {
    this.setState({
      visible: true
    });
  };
  onClose = () => {
    this.setState({
      visible: false
    });
  };
  onTabChange = (key, type) => {
    this.setState({ [type]: key });
  };
  contentListNoTitle = {
    cotisation: <Cotisation />
  };

  render() {
    return (
      <div>
        <MyContext.Consumer>
          {context => (
            <React.Fragment>
              <a
                href="#top"
                type="primary"
                shape="circle"
                icon="edit"
                size="small"
                onClick={this.showDrawer}
              >
                {context.state.contrat.numeroContrat}
              </a>
              <Drawer
                title={
                  <div style={{ fontSize: "18px" }}>
                    Dtails du Contrat : {context.state.contrat.numeroContrat}
                  </div>
                }
                width={1400}
                onClose={this.onClose}
                visible={this.state.visible}
                bodyStyle={{ paddingBottom: 80 }}
              >
                <Card
                  tabList={tabListNoTitle}
                  activeTabKey={this.state.noTitleKey}
                  onTabChange={key => {
                    this.onTabChange(key, "noTitleKey");
                  }}
                >
                  {this.contentListNoTitle[this.state.noTitleKey]}
                </Card>
                <div className="submit-cancel">
                  <Button
                    type="primary"
                    className="not-rounded"
                    onClick={this.onClose}
                  >
                    Fermer
                  </Button>
                </div>
              </Drawer>
            </React.Fragment>
          )}
        </MyContext.Consumer>
      </div>
    );
  }
}
export default DetailsCotisation;
