/* eslint-disable react/prop-types */
import "antd/dist/antd.css";
import {
  Button,
  Col,
  DatePicker,
  Form,
  Icon,
  Input,
  message,
  Modal,
  notification,
  Row,
  Select,
  Table
} from "antd";
import React, { Component } from "react";
import { NOM_ASSURE, CIN_ASSURE, NUM_CONTRAT } from "../../constants/index";
import { getDecesContrat, searchContrat } from "../GestionContrats/ContratsAPI";
import { getColumnSearchProps } from "../Parametrage/ProduitRetraite/ProduitRetraiteServices/getColumnSearchProps";
import { ajoutCotisation, getPrelevement } from "./CotisationAPI";
import DetailsCotisation from "./DetailsCotisation";

export const MyContext = React.createContext();
var statuts = "ACCEPTED";
const { Option } = Select;
const { Search } = Input;
const dateFormat = "DD-MM-YYYY";
let id = "";
class Comptabilite extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      visible: false,
      dateCreation: new Date(),
      searchText: "",
      searchedColumn: "",
      selectedRowKeys: [],
      searchby: NUM_CONTRAT,
      searchfor: "",
      selectedFile: null,
      record: {},
      contrats: [],
      totalItems: 0,
      pagenumber: 1,
      tauxSurprime: null,
      montantSurprime: null,
      montantCotisation: null,
      cotisationTotale: null
    };
    this.columns = [
      {
        title: "Numro quittance",
        dataIndex: "numQuittance",
        key: "numQuittance",
        ...getColumnSearchProps("numQuittance", "numQuittance1", this)
      },
      {
        title: "N de Contrat",
        dataIndex: NUM_CONTRAT,
        key: "numcontrat",
        render: (text, record) => {
          return (
            <span>
              <div>
                <MyContext.Provider
                  value={{
                    state: record
                  }}
                >
                  <DetailsCotisation />
                </MyContext.Provider>
              </div>
            </span>
          );
        }
      },
      {
        title: "Dure Contrat",
        dataIndex: "contrat.dureeContrat",
        key: "dureeContrat",
        ...getColumnSearchProps("dureeContrat", "Dure contrat", this)
      },
      {
        title: "CIN assur",
        dataIndex: "contrat.assure.cin",
        key: "cinAssure"
      },
      {
        title: "Nom assur",
        dataIndex: "contrat.assure.nom",
        key: "nomAssure",
        ...getColumnSearchProps("assure.nom", "Prnom assure", this)
      },
      {
        title: "Prnom assur",
        dataIndex: "contrat.assure.prenom",
        key: "prenomAssure"
      },
      {
        title: "Nom souscripteur",
        dataIndex: "contrat.souscripteur.nom",
        key: "nomSouscripteur",
        ...getColumnSearchProps("souscripteur.nom", "Prnom assure", this)
      },
      {
        title: "Prnom souscripteur",
        dataIndex: "contrat.souscripteur.prenom",
        key: "prenomSouscripteur"
      },

      {
        title: "Date d'effet",
        dataIndex: "contrat.dateEffet",
        key: "dateEffet"
      },
      {
        title: "Date cheance",
        dataIndex: "contrat.dateEcheance",
        key: "dateEcheance"
      },
      {
        title: "Produit",
        dataIndex: "contrat.produit.libelle",
        key: "produit"
      },
      {
        title: "Etat cotisation",
        dataIndex: "etatCotisation",
        key: "etatCotisation"
      },
      {
        title: "Action",
        dataIndex: "operation",
        fixed: "right",
        render: (text, record) => {
          return (
            <span>
              <Button
                type="primary"
                onClick={() => this.handleChange(record)}
                size="small"
                style={{
                  borderRadius: "0px",
                  width: "105px",
                  backgroundColor: "#4ee289",
                  borderColor: "#4ee289",
                  textAlign: "left"
                }}
              >
                <Icon type="plus-circle" /> Cotisation
              </Button>
            </span>
          );
        }
      }
    ];
  }
  componentDidMount() {
    this.recherchePrelevement();
  }
  recherchePrelevement = () => {
    getPrelevement()
      .then(response => {
        let newDataList = [];
        response.data.content.forEach(element => {
          let key = { key: element.id };
          element = { ...element, ...key };
          newDataList.push(element);
        });

        this.setState({
          data: newDataList
        });
      })
      .catch(error => {
        if (error.status === 401) {
          notification.error({
            message: "Takaful",
            description:
              "Votre login ou mot de passe est incorrect. Veuillez ressayer!"
          });
        } else {
          notification.error({
            message: "Takaful",
            description:
              error.message ||
              "Dsol! Quelque chose s'est mal pass Veuillez ressayer!"
          });
        }
      });
  };
  componentDidUpdate(_, prevState) {
    if (
      prevState.montantCotisation !== this.state.montantCotisation &&
      this.state.montantCotisation !== null
    ) {
      this.setState({
        montantSurprime:
          (this.state.tauxSurprime * this.state.montantCotisation) / 100
      });
      this.setState({
        cotisationTotale:
          this.state.montantSurprime + this.state.montantCotisation
      });
    }
  }
  handleChange = value => {
    id = value.contrat.id;
    this.setState({ visible: true, tauxSurprime: value.contrat.tauxSurprime });
  };
  onChangeMontantCotisation = e => {
    this.setState({ montantCotisation: e.target.value });
  };
  async getproprties(statut, page, limit, sort, direction) {
    let contratsResponse = await getDecesContrat(
      statut,
      page,
      limit,
      sort,
      direction
    );
    this.setState({
      contrats: [...contratsResponse.data.content],
      totalItems: contratsResponse.data.totalElements
    });
  }

  async searchContrats(page, limit, searchby, searchfor) {
    let contratResponse = await searchContrat(page, limit, searchby, searchfor);

    if (searchby === CIN_ASSURE || searchby === NOM_ASSURE) {
      this.setState({
        data: [...contratResponse.data.content],
        totalItems: contratResponse.data.numberOfElements
      });
    } else if (searchby === NUM_CONTRAT) {
      this.setState({
        data: [...contratResponse.data.content],
        totalItems: contratResponse.data.totalElements
      });
    }
  }
  onPaginationChange = (pagenumber, pagesize) => {
    this.state.searchfor === ""
      ? this.getproprties(statuts, pagenumber - 1, 5, NUM_CONTRAT, "desc")
      : this.searchContrats(
          pagenumber - 1,
          5,
          this.state.searchby,
          this.state.searchfor
        );
  };
  selectBefore = (
    <Select
      onChange={value => this.setState({ searchby: value })}
      defaultValue={NUM_CONTRAT}
      style={{ width: 150 }}
    >
      <Option value={NUM_CONTRAT}>N de contrat</Option>
      <Option value={CIN_ASSURE}>CIN</Option>
      <Option value={NOM_ASSURE}>Nom Assur</Option>
    </Select>
  );
  search = value => {
    this.setState({ pagenumber: 1, searchfor: value });
    this.searchContrats(
      this.state.pagenumber - 1,
      2,
      this.state.searchby,
      value
    );
  };

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex
    });
  };
  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: "" });
  };
  handleSubmited = e => {
    e.preventDefault();

    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({ visible: false });
        let requestModel = {
          datePrelevement: values.datePrelevement,
          montantCotisation: values.montantCotisation,
          montantTTC: values.montantTTC,
          montantTaxe: values.montantTaxe,
          contrat: id
        };

        ajoutCotisation(requestModel)
          .then(response => {
            notification.success({
              message: "TAKAFUL",
              description: "L'insertion est bien faite"
            });
            window.location.reload();
          })
          .catch(error => {
            notification.error({
              message: "TAKAFUL",
              description:
                error.message ||
                "Sorry! Something went wrong. Please try again!"
            });
          });
      } else {
        message.warning("Contrat non couvert au date de sinistre");
      }
    });
  };
  handleCanceled = () => {
    this.setState({ visible: false });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { visible } = this.state;
    const formItemLayout = {
      labelCol: {
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 12 }
      }
    };

    return (
      <div>
        <div style={{ marginBottom: 16 }}>
          <Search
            placeholder="Entrez le text de recherche"
            addonBefore={this.selectBefore}
            onSearch={value => this.search(value)}
            enterButton
            style={{ width: 600 }}
          />
        </div>
        <Table
          bordered
          rowClassName="editable-row"
          columns={this.columns}
          dataSource={this.state.data}
          pagination={{
            onChange: this.onPaginationChange,
            pageSize: 5,
            total: this.state.totalItems
          }}
          size="small"
          tableLayout="unset"
          scroll={{ x: 1300 }}
        />
        <Modal
          visible={visible}
          title="Veuillez saisir les informations d'une cotisation"
          onOk={this.handleSubmited}
          onCancel={this.handleCanceled}
          afterClose={this.handleClosed}
          style={{ top: 20 }}
          width="1000px"
          footer={[
            <Button
              key="back"
              onClick={this.handleCanceled}
              className="not-rounded"
            >
              Fermer
            </Button>,
            <Button
              key="submit"
              type="primary"
              onClick={this.handleSubmited}
              className="not-rounded"
            >
              Ajouter
            </Button>
          ]}
        >
          <Form {...formItemLayout}>
            <div>
              <Row>
                <Col span={11}>
                  <Form.Item label="Effet Emission">
                    {getFieldDecorator("datePrelevement")(
                      <DatePicker
                        format={dateFormat}
                        style={{ width: "100%" }}
                        className="not-rounded"
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={11} offset={1}>
                  <Form.Item label="Montant Cotisation">
                    {getFieldDecorator("montantCotisation")(
                      <Input
                        className="not-rounded"
                        onChange={this.onChangeMontantCotisation}
                      />
                    )}
                  </Form.Item>
                </Col>
              </Row>
              <Row>
                <Col span={11}>
                  <Form.Item label="Montant Taxe">
                    {getFieldDecorator("montantTaxe")(
                      <Input className="not-rounded" />
                    )}
                  </Form.Item>
                </Col>
                <Col span={11} offset={1}>
                  <Form.Item label="Montant TTC">
                    {getFieldDecorator("montantTTC")(
                      <Input className="not-rounded" />
                    )}
                  </Form.Item>
                </Col>
              </Row>
            </div>
          </Form>
        </Modal>
      </div>
    );
  }
}
export default Form.create()(Comptabilite);
